import React, { useContext } from 'react';
import "./navbar.css";
import logo from "../../img/logo.png";
import basket from "../../img/basketblue.png";
import {BasketContext} from '../basket/basket';

const Navbar = () => {
    const {count}=useContext(BasketContext);
    return ( 
        <div className="navbar">
            <div className="logo">
                <img src={logo} alt="logo"/>
            </div>
            <div className='navbar_links'>
                <span>Books</span>
                <span>Blog</span>
                <span>Contact</span>
                <span>Login</span>
                <div className="navbar_basket">
                    <p className="navbar_basket_b">
                    <img src={basket} alt="basket"/>Basket</p>
                    <p className="navbar_basket_n">{count}</p>
                </div>
            </div>
        </div>
     );
}
 
export default Navbar;