import React, { useState } from 'react';
import "./second_fold.css";
import slide1 from "../../img/download.jpg";
import slide2 from "../../img/download (1).jpg";
import slide3 from "../../img/pexels-photo-5530681.jpeg";
import slide4 from "../../img/pexels-photo-5604947.jpeg";
import slide5 from "../../img/pexels-photo-5858998.jpeg";

const Secondfold = () => {
    const [showMessage1, setshowMessage1] = useState(true);
    const [showMessage2, setshowMessage2] = useState(false);
    const [showMessage3, setshowMessage3] = useState(false);
    const [showMessage4, setshowMessage4] = useState(false);
    const [showMessage5, setshowMessage5] = useState(false);
    const handleClick2=()=>{
        setshowMessage2(true);
        setshowMessage1(false);
        setshowMessage3(false);
        setshowMessage4(false);
        setshowMessage5(false);
    }
    const handleClick1=()=>{
        setshowMessage1(true);
        setshowMessage2(false);
        setshowMessage3(false);
        setshowMessage4(false);
        setshowMessage5(false);
    }
    const handleClick3=()=>{
        setshowMessage3(true);
        setshowMessage1(false);
        setshowMessage2(false);
        setshowMessage4(false);
        setshowMessage5(false);
    }
    const handleClick4=()=>{
        setshowMessage4(true);
        setshowMessage1(false);
        setshowMessage3(false);
        setshowMessage2(false);
        setshowMessage5(false);
    }
    const handleClick5=()=>{
        setshowMessage5(true);
        setshowMessage1(false);
        setshowMessage3(false);
        setshowMessage4(false);
        setshowMessage2(false);
    }
    return ( 
    <div className="second_fold">
        <div className="second_fold_text">
            <div className="text one">
                <div className="text_title">
                   <h3>Including success stories of Mark and Musk</h3>{showMessage1 ? 
                    (<span onClick={setshowMessage1.bind(null, false)}>-</span>)
                    :
                    (<span onClick={handleClick1}>+</span>)}
                </div>   
                {showMessage1&&<p className={showMessage1 ? "show" : "hide"}>Author express the huge requirement of new ideas and uniquenes</p>} 
            </div>
            <hr/>
            <div className="text two">
                <div className="text_title">
                   <h3>Must Read!</h3>{showMessage2 ? 
                    (<span onClick={setshowMessage2.bind(null,false)}>-</span>)
                    :
                    (<span onClick={handleClick2}>+</span>)}
                </div>   
                {showMessage2&&<p className={showMessage2 ? "show" : "hide"}>Most picked books which u should perfer them on many of the books</p>} 
            </div>
            <hr/>
            <div className="text three">
                <div className="text_title">
                   <h3>Best book for enterpreneurs.</h3>{showMessage3 ? 
                    (<span onClick={setshowMessage3.bind(null, false)}>-</span>)
                    :
                    (<span onClick={handleClick3}>+</span>)}
                </div>   
                {showMessage3&&<p className={showMessage3 ? "show" : "hide"}>Bill Gates is at it again. He's indulging in one of his favorite habits: reading. Gates recently wrote a blog post titled, "5 amazing books I read this year."</p>} 
            </div>
            <hr/>
            <div className="text four">
                <div className="text_title">
                   <h3>Zero to one is not a single step ...</h3>{showMessage4 ? 
                    (<span onClick={setshowMessage4.bind(null, false)}>-</span>)
                    :
                    (<span onClick={handleClick4}>+</span>)}
                </div>   
                {showMessage4&&<p className={showMessage4 ? "show" : "hide"}> Its about questioning and rethinking received wisdom to create the future.</p>} 
            </div>
            <hr/>
            <div className="text five">
                <div className="text_title">
                   <h3>Recommendations for improvement</h3>{showMessage5 ? 
                    (<span onClick={setshowMessage5.bind(null, false)}>-</span>)
                    :
                    (<span onClick={handleClick5}>+</span>)}
                </div>   
                {showMessage5&&<p className={showMessage5 ? "show" : "hide"}>Things which could let you put urself at better par in life</p>} 
            </div>
            <hr/>
        </div>
        <div className="second_fold_img">
            {showMessage1&&<img src={slide1} alt="book1"/>}       
            {showMessage2&&<img src={slide2} alt="book2"/>}
            {showMessage3&&<img src={slide3} alt="book3"/>}
            {showMessage4&&<img src={slide4} alt="book4"/>}
            {showMessage5&&<img src={slide5} alt="book5"/>}               
        </div>            
    </div>    
    );
}
 
export default Secondfold;
