import React, { useContext} from 'react';
import "./first_fold.css";
import basket from '../../img/basket.png'
import {BasketContext} from '../basket/basket';

const Firstfold = () => {
    const {removeFrombasket,addTobasket,count}=useContext(BasketContext) || {};
    const handlePlus=(e)=>{
        e.preventDefault();
        addTobasket();
    }
    const handleMinus=(e)=>{
        e.preventDefault();
        removeFrombasket();
    }
    return (
    <div className="firstfold_con"> 
        <div className="firstfold">
            <div className="firstfold_container">
                <div className="firstfold_text">
                    <h1 className="firstfold_text_title">Zero to One</h1>
                    <p className="firstfold_text_p">The next Bill Gates will not an operating system.The next Larry Page or Sergey Bin won't make a search engine.If you are copying these guys,you aren't learning from them.It's easier to copy a model than to make something new</p>
                </div>
                <div className="firstfold_card">
                    <p><span className="bold">Publisher:</span>Random House;2014 edition(18 September 2014)</p>
                    <hr/>
                    <p><span className="bold">Paperback:</span>210 pages</p>
                    <hr/>
                    <p><span className="bold">Language:</span>English</p>
                    <hr/>
                    <p><span className="bold">ISBN-10:</span>9780753555194</p>
                    <hr/>
                    <p><span className="bold">ISBN-13:</span>978-0753555194</p>
                    <div className="card_price">
                        <span className='price'>120</span>
                        <div className="card_box"> 
                            <span className="card_add" onClick={handleMinus}>-</span>
                            <span className="card_add">{count}</span>
                            <span className="card_add" value={count} onClick={handlePlus}>+</span>
                        </div>
                    </div>
                    <button onClick={handlePlus} className="add_to"><img src={basket} alt="basket"/>Add To Basket</button>
                </div>
            </div>
        </div>
    </div>    
     );
}
 
export default Firstfold;